<template>
    <div class="login-container">
        <h1>Sign In</h1>
        <div class="login-box">
            <form @submit.prevent="handleLogin">
                <label for="role">Role:</label>
                <select v-model="role" id="role" required>
                  <option value="manager">Manager</option>
                  <option value="driver">Driver</option>
                </select>
                <br />
                <label for="username">Username:</label>
                <input type="text" id="username" v-model="username" required />
                <br />
                <label for="password">Password:</label>
                <input type="password" id="password" v-model="password" required />
                <br />
                <button type="submit">Sign In</button>
              </form>
              <p v-if="errorMessage" id="errorMessage">{{ errorMessage }}</p>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
          return {
            role: "",
            username: "",
            password: "",
            errorMessage: "",
          };
        },
        mounted() {
          const link = document.createElement("link");
          link.rel = "stylesheet";
          link.href = "@/assets/css/signin.css"; // signin.css   
          link.id = "signin-styles"; //     ID 
          document.head.appendChild(link);
        },
        beforeDestroy() {
          const link = document.getElementById("signin-styles");
          if (link) {
            document.head.removeChild(link); //    
          }
        },
        methods: {
          handleLogin() {
            if (this.role === "manager" && this.username === "manager001" && this.password === "1234") {
              localStorage.setItem("loggedIn", "true"); //   
              this.$router.push({name: "dashboard"}); //   
            } else if (this.role === "driver" && this.username === "driver001" && this.password === "5678") {
              this.$router.push({name: "driver"});  //   
            } else {
              this.errorMessage = "Invalid username or password.";
            }
          },
        },
      };
</script>

<style scoped>
    /* signin.css   
    @import "@/assets/css/signin.css"; */
    </style>